import './bbutil'


import BBIFrame from './BBIFrame';




class BBSVGEditor extends BBIFrame{
  
 
  getUrl = () => `/${window.bbFolderName}/static/iframes/svgeditor/index.html`


  constructor() {
    super()

    this.scrolling = 'no';
    this.noEvents = false;
    this.usesParameters=false;
  }




  
}
export default BBSVGEditor
